body {
  color: #fff;
  background: black;
}

.wrap {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 800px;
  height: 450px;

  .video-icon {
    position: absolute;
    z-index: 999999;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    cursor: pointer;
    transition: opacity 0.1s;
    opacity: 1;

    &:after {
      content: "";
      display: block;
      background-size: contain;
      background-position: top;
      background-image: url("/img/playBtn.svg");
      background-repeat: no-repeat;
      width: 87.82px;
      height: 87.82px;
      z-index: 999;
    }
  }

  .wrap_controls {
    width: 90%;
    height: 100%;
    position: relative;
    z-index: 9999;
    left: 50%;
    transform: translate(-52%);

    &-left,
    &-right {
      height: 40px;
      width: 40px;
      position: absolute;
      top: 45%;
      z-index: 9999;
      transform: translate(-50%, 0);
      cursor: pointer;
    }
    &-left {
      background-image: url("../img/left.svg");
      left: 2rem;
    }
    &-right {
      background-image: url("../img/right.svg");
      right: -1.5rem;
    }
  }

  .video {
    position: absolute;
    top: 0;
    left: 0;
    transition: all 0.2s ease;
    width: 800px;
    height: 450px;
    outline: none;
    z-index: 0;

    &:first-child {
      z-index: 0;
    }

    &:nth-child(2) {
      left: 60px;
      z-index: -1;
      transform: scale(0.9);
    }
    &:nth-child(3) {
      left: 125px;
      z-index: -2;
      transform: scale(0.8);
    }
    &:nth-child(4) {
      left: 180px;
      z-index: -3;
      transform: scale(0.7);
    }
  }
}

.push-forward {
  transform: scale(1) translate(-110%, 0) !important;
  left: 0 !important;
}
.push-back {
  transform: translate(0, 0);
}
.block-btn {
  pointer-events: none;
  opacity: 0.5;
}

.dropZindex {
  z-index: -999999!important;
}
.raiseZindex {
  z-index: 0!important;
}
.active_slide {
  z-index: 9999!important;
}


